import { BpmnModel } from "../BpmnModel";
import { BpmnModelHelper } from "../BpmnModelHelper";
import { Font } from "../omgdc/Font";
import { Style } from "../omgdi/Style";

export class BPMNLabelStyle extends Style {
    constructor(model: BpmnModel) {
        super(model);
    }

    Font: Font;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.Font = null;
    }

    ConvertToJsonObject(): any {
        let obj = super.ConvertToJsonObject();

        if (this.Font != null) {
            BpmnModelHelper.SetAttributeValue(obj, "Font", this.Font.ConvertToJsonObject());
        }

        return obj;
    }

    LoadFromJsonObject(obj: any): void {
        super.LoadFromJsonObject(obj);

        if (obj.hasOwnProperty("Font")) {
            this.Font = new Font(this.Model);
            this.Font.LoadFromJsonObject(obj.Font);
        }
    }
}